@import 'src/assets/variables.scss';

.productItem {
  box-shadow: 0 0 15px rgb(0 0 0 / 10%);
  border-radius: 25px;
  padding: 16px;
  margin: 0 17px 65px 17px;
  max-width: 280px;
  width: 100%;
  display: flex;
  flex-direction: column;
  transition: all .3s ease;

  @media (max-width: 720px) {
    max-width: 100%;
  }

  &:hover {
    box-shadow: 0 0 15px rgb(0 0 0 / 50%);
  }

  > a {
    text-align: center;
  }

  > a > img {
    margin-bottom: 11px;
    max-width: 259.12px;
    height: 260px;
    object-fit: cover;
    transition: all .2s ease-in-out;

    &:hover {
      transform: scale(0.9);
    }
  }

  > h3 {
    text-align: center;
    margin-bottom: 22px;
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;
    color: $black-color;
    flex-grow: 1;
  }

  &__list {
    background-color: $product-color-sort;
    border-radius: 10px;
    width: 100%;
    min-height: 85px;
    padding: 7px;
    text-align: center;
    margin-bottom: 17px;
  }

  &__list_box {
    display: flex;
    flex: 1 1;
    margin: 0 -3px;

    li {
      cursor: pointer;
      margin: 0 6px;
      border-radius: 5px;
    }
  }

  &__list_top {
    margin-bottom: 7px;
  }

  &__item_top {
    flex: 1 1;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    padding: 7px 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
    border-radius: 5px;
  }

  &__item_bottom {
    padding: 6px;
    flex: 1 1;
  }

  &__list_active {
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
    border-radius: 5px;
    background: $background-color-product;
    color: $while-color;

    //&:active {
    //  transform: translateY(3px);
    //}
  }

  // bottom
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {
    font-weight: 800;
    font-size: 22px;
    line-height: 27px;
    color: $black-color;
  }

}